.column {
  font-size: 12px;
  line-height: 1.4;
  color: #ececec;
  background: #334247;
  width: calc(100% / 3 - 10px);
  overflow: hidden;
  padding: 30px 0;
  display: flex;
  flex-direction: column;
  margin: 0 5px;
  position: relative; }
  .column--green .column__price {
    background: #5bb189; }
  .column--green .column__list-item::before {
    color: #5bb189; }
  .column--green .column__btn {
    background: #5bb189; }
  .column--blue .column__price {
    background: #3a6ba5; }
  .column--blue .column__list-item::before {
    color: #3a6ba5; }
  .column--blue .column__btn {
    background: #3a6ba5; }
  .column--orange .column__price {
    background: #ec632a; }
  .column--orange .column__list-item::before {
    color: #ec632a; }
  .column--orange .column__btn {
    background: #ec632a; }
  @media (max-width: 767px) {
    .column {
      width: 100%;
      max-width: 400px;
      margin: 0 auto 30px; } }
  .column__wrapper {
    background: #141b1c;
    height: 100%;
    padding: 100px 0; }
  .column__container {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto; }
  .column__row {
    display: flex;
    flex-wrap: wrap; }
  .column__title {
    font-size: 26px;
    text-align: center;
    font-weight: bold;
    color: #fff; }
  .column__subtitle {
    text-align: center;
    font-size: 14px;
    margin-bottom: 30px; }
  .column__price {
    font-size: 40px;
    font-weight: bold;
    padding: 15px 0;
    text-align: center;
    color: #fff; }
  .column__description {
    padding: 30px 10px;
    text-align: center; }
  .column__list {
    padding: 0;
    margin: 0 0 40px 0; }
    .column__list-item {
      padding: 15px 20px 15px 40px;
      list-style-type: none;
      position: relative; }
      .column__list-item:nth-child(2n+1) {
        background: #2e3b3f; }
      .column__list-item::before {
        position: absolute;
        content: '✓';
        font-size: 16px;
        font-weight: bold;
        display: block;
        top: 50%;
        left: 20px;
        transform: translateY(-50%); }
  .column__btn {
    margin: auto auto 0;
    width: fit-content;
    text-transform: uppercase;
    padding: 10px 30px;
    border-radius: 30px;
    cursor: pointer; }
  .column__sticker {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px 100px;
    color: #fff;
    background: #dc3f45;
    font-weight: bold;
    text-transform: uppercase;
    transform: rotate(45deg) translate(30%, -200%); }
